import React from "react";
import Checkbox from "@material-ui/core/Checkbox";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Typography from "@material-ui/core/Typography";
import { useNode } from "@craftjs/core";
import RecordForm, { extractValues } from "../../common/RecordForm";
import Accordion from "@material-ui/core/Accordion";
import AccordionDetails from "@material-ui/core/AccordionDetails";
import AccordionSummary from "@material-ui/core/AccordionSummary";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";

import ViewSettings from "../ViewSettings";

function CheckboxComponent({ text }) {
    const {
        connectors: { connect, drag },
        selected,
        dragged,
    } = useNode((state) => ({
        selected: state.events.selected,
        dragged: state.events.dragged,
    }));

    return (
        <FormControlLabel
            ref={(ref) => connect(drag(ref))}
            control={<Checkbox color="primary" />}
            label={text}
        />
    );
}

const groups = [
    {
        label: "Basic",
        children: [
            {
                label: "Color",
                identifier: "color",
                type: "select",
                options: [
                    {
                        value: "default",
                        title: "Default",
                    },
                    {
                        value: "primary",
                        title: "Primary",
                    },
                    {
                        value: "secondary",
                        title: "Secondary",
                    },
                ],
                required: false,
                readOnly: false,
                quickAdd: true,
                unique: false,
                hidden: false,
                tooltip: "The text alignment can be left, center, or right.",
                multipleValues: false,
                defaultValue: "default",
            },
            {
                label: "Size",
                identifier: "size",
                type: "select",
                options: [
                    {
                        value: "medium",
                        title: "Medium",
                    },
                    {
                        value: "small",
                        title: "Small",
                    },
                ],
                required: false,
                readOnly: false,
                quickAdd: true,
                unique: false,
                hidden: false,
                tooltip: "The text alignment can be left, center, or right.",
                multipleValues: false,
                defaultValue: "medium",
            },
            {
                label: "Checked",
                identifier: "checked",
                type: "switch",
                required: true,
                readOnly: false,
                quickAdd: false,
                unique: false,
                hidden: false,
                tooltip: "The URL to link to when the Checkbox is clicked.",
                defaultValue: false,
            },
            {
                label: "Required",
                identifier: "required",
                type: "switch",
                required: true,
                readOnly: false,
                quickAdd: false,
                unique: false,
                hidden: false,
                tooltip: "The URL to link to when the Checkbox is clicked.",
                defaultValue: false,
            },
        ],
    },
];

function CheckboxSettings() {
    const {
        actions: { setProp },
        size,
        variant,
        color,
        text,
        fullWidth,
        ripple,
        required,
    } = useNode((node) => ({
        size: node.data.props.size,
        variant: node.data.props.variant,
        color: node.data.props.color,
        text: node.data.props.text,
        ripple: node.data.props.ripple,
        fullWidth: node.data.props.fullWidth,
        required: node.data.props.required,
    }));

    const [saveDisabled, setSaveDisabled] = React.useState(true);
    const [values, setValues] = React.useState({
        ...extractValues(groups),
        size,
        variant,
        color,
        text,
        fullWidth,
        ripple,
    });
    const handleValueChange = (field, value) => {
        const newValues = JSON.parse(JSON.stringify(values));
        newValues[field.identifier] = value;

        setValues(newValues);

        setProp((props) => Object.assign(props, newValues));
    };

    return (
        <div>
            <ViewSettings />
            <Accordion>
                <AccordionSummary
                    expandIcon={<ExpandMoreIcon />}
                    id="panel1bh-header"
                >
                    <Typography>Checkbox Properties</Typography>
                </AccordionSummary>
                <React.Fragment>
                    <AccordionDetails>
                        <RecordForm
                            groups={groups}
                            values={values}
                            onValueChange={handleValueChange}
                            tabIndex={0}
                            showMore={true}
                            onValidityChange={setSaveDisabled}
                        />
                    </AccordionDetails>
                </React.Fragment>
            </Accordion>
        </div>
    );
}

CheckboxComponent.craft = {
    defaultProps: {
        text: "CLICK HERE!",
        fullWidth: false,
        ripple: true,
    },
    related: {
        settings: CheckboxSettings,
    },
};

export default CheckboxComponent;
