import React from "react";
import RadioGroup from "@material-ui/core/RadioGroup";
import { Element, useNode } from "@craftjs/core";
import RecordForm, { extractValues } from "../../common/RecordForm";
import Accordion from "@material-ui/core/Accordion";
import AccordionDetails from "@material-ui/core/AccordionDetails";
import AccordionSummary from "@material-ui/core/AccordionSummary";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import Typography from "@material-ui/core/Typography";
import ViewSettings from "../ViewSettings";

function RadioGroupComponent(props) {
    const { value, children } = props;
    const {
        connectors: { connect, drag },
        actions: { setProp },
    } = useNode((state) => ({
        selected: state.events.selected,
        dragged: state.events.dragged,
    }));

    return <div ref={(ref) => connect(drag(ref))}>Hello</div>;
}

const groups = [
    {
        label: "Basic",
        children: [
            {
                label: "Value",
                identifier: "value",
                type: "text",
                required: true,
                readOnly: false,
                quickAdd: false,
                unique: false,
                hidden: false,
                tooltip: "The text of the button.",
                multipleValues: false,
                defaultValue: "CLICK HERE!",
            },
        ],
    },
];

function RadioGroupSettings() {
    const {
        actions: { setProp },
        value,
    } = useNode((node) => ({
        value: node.data.props.value,
    }));

    const [saveDisabled, setSaveDisabled] = React.useState(true);
    const [values, setValues] = React.useState({
        ...extractValues(groups),
        value,
    });
    const handleValueChange = (field, value) => {
        const newValues = JSON.parse(JSON.stringify(values));
        newValues[field.identifier] = value;

        setValues(newValues);

        setProp((props) => Object.assign(props, newValues));
    };

    return (
        <div>
            <ViewSettings />
            <Accordion>
                <AccordionSummary
                    expandIcon={<ExpandMoreIcon />}
                    id="panel1bh-header"
                >
                    <Typography>Button Properties</Typography>
                </AccordionSummary>
                <React.Fragment>
                    <AccordionDetails>
                        <RecordForm
                            groups={groups}
                            values={values}
                            onValueChange={handleValueChange}
                            tabIndex={0}
                            showMore={true}
                            onValidityChange={setSaveDisabled}
                        />
                    </AccordionDetails>
                </React.Fragment>
            </Accordion>
        </div>
    );
}

RadioGroupComponent.craft = {
    defaultProps: {
        value: "",
    },
    related: {
        settings: RadioGroupSettings,
    },
};

export default RadioGroupComponent;
