import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import { useNode } from "@craftjs/core";

import GridSettings from "./GridSettings";
import GridBase from "./GridBase";

const useStyles = makeStyles((theme) => ({
    root: {
        margin: 0,
        width: "100%",
    },
    heading: {
        fontSize: theme.typography.pxToRem(15),
    },
    selected: {
        outlineWidth: 1,
        outlineStyle: "dashed",
        outlineColor: "#4791db",
    },
}));

const lengthCssUnits = {
    vh: "vh",
    vw: "vw",
    px: "px",
    cm: "cm",
    mm: "mm",
    in: "in",
    pt: "pt",
    "%": "%",
};

const angleCssUnits = {
    deg: "deg",
    rad: "rad",
    grad: "grad",
};

function makeTransform(
    rotate,
    rotateUnit,
    translateX,
    translateXUnit,
    translateY,
    translateYUnit,
    scaleX,
    scaleY,
    skewX,
    skewXUnit,
    skewY,
    skewYUnit
) {
    const result = [];

    if (rotate) {
        result.push(`rotate(${rotate}${angleCssUnits[rotateUnit]})`);
    }

    if (translateX) {
        result.push(
            `translateX(${translateX}${lengthCssUnits[translateXUnit]})`
        );
    }

    if (translateY) {
        result.push(
            `translateY(${translateY}${lengthCssUnits[translateYUnit]})`
        );
    }

    if (scaleX) {
        result.push(`scaleX(${scaleX})`);
    }

    if (scaleY) {
        result.push(`scaleY(${scaleY})`);
    }

    if (skewX) {
        result.push(`skewX(${skewX}${angleCssUnits[skewXUnit]})`);
    }

    if (skewY) {
        result.push(`skewY(${skewY}${angleCssUnits[skewYUnit]})`);
    }

    return result.join(" ") || "none";
}

function makeBackdropFilter(
    blur,
    blurUnit,
    brightness,
    contrast,
    grayscale,
    hueRotate,
    hueRotateUnit,
    invert,
    opacity,
    saturate,
    sepia
) {
    const result = [];

    if (blur) {
        result.push(`blur(${blur}${lengthCssUnits[blurUnit]})`);
    }

    if (brightness) {
        result.push(`brightness(${brightness})`);
    }

    if (contrast) {
        result.push(`contrast(${contrast})`);
    }

    if (grayscale) {
        result.push(`grayscale(${grayscale})`);
    }

    if (hueRotate) {
        result.push(`hue-rotate(${hueRotate}${angleCssUnits[hueRotateUnit]})`);
    }

    if (invert) {
        result.push(`invert(${invert})`);
    }

    if (opacity) {
        result.push(`opacity(${opacity})`);
    }

    if (saturate) {
        result.push(`saturate(${saturate})`);
    }

    if (sepia) {
        result.push(`sepia(${sepia})`);
    }

    return result.join(" ") || "none";
}

function GridComponent(props) {
    const {
        children,

        // Layout
        layoutDirection,

        // Background
        backgroundAttachment,
        backgroundBlendMode,
        backgroundClip,
        backgroundImage,
        backgroundColor,
        backgroundOrigin,
        backgroundPosition,
        backgroundRepeat,
        backgroundSize,

        // Border
        borderColor,
        borderStyle,
        borderWidth,
        borderWidthUnit,

        // Border Image
        borderImageSource,
        borderImageOutset,
        borderImageOutsetUnit,
        borderImageRepeat,
        borderImageSlice,
        borderImageWidth,
        borderImageWidthUnit,

        // Box
        boxSizing,
        boxShadow,

        // Dimension
        width,
        widthUnit,
        height,
        heightUnit,
        minWidth,
        minWidthUnit,
        minHeight,
        minHeightUnit,
        maxWidth,
        maxWidthUnit,
        maxHeight,
        maxHeightUnit,

        // Margin
        marginTop,
        marginTopUnit,
        marginRight,
        marginRightUnit,
        marginBottom,
        marginBottomUnit,
        marginLeft,
        marginLeftUnit,

        // Padding
        paddingTop,
        paddingTopUnit,
        paddingRight,
        paddingRightUnit,
        paddingBottom,
        paddingBottomUnit,
        paddingLeft,
        paddingLeftUnit,

        /// Transform
        transformRotate,
        transformRotateUnit,
        transformTranslateX,
        transformTranslateXUnit,
        transformTranslateY,
        transformTranslateYUnit,
        transformScaleX,
        transformScaleY,
        transformSkewX,
        transformSkewXUnit,
        transformSkewY,
        transformSkewYUnit,

        // Outline
        outlineStyle,
        outlineWidth,
        outlineWidthUnit,
        outlineColor,
        outlineOffset,
        outlineOffsetUnit,

        // Background Filter
        backgroundFilterBlur,
        backgroundFilterBlurUnit,
        backgroundFilterBrightness,
        backgroundFilterContrast,
        backgroundFilterGreyscale,
        backgroundFilterHueRotate,
        backgroundFilterHueRotateUnit,
        backgroundFilterInvert,
        backgroundFilterOpacity,
        backgroundFilterSaturate,
        backgroundFilterSepia,
    } = props;
    const classes = useStyles();
    const {
        connectors: { connect, drag },
        selected,
    } = useNode((state) => ({
        selected: state.events.selected,
        dragged: state.events.dragged,
    }));

    console.log(props);
    // alert(width);

    let outlineProperties = {};
    if (!selected) {
        outlineProperties = {
            outlineStyle,
            outlineWidth: `${outlineWidth}${lengthCssUnits[outlineWidthUnit]}`,
            outlineColor: `${outlineColor}`,
            outlineOffset: `${outlineOffset}${lengthCssUnits[outlineOffsetUnit]}`,
        };
    }

    console.log(backgroundBlendMode);

    return (
        <GridBase
            direction={layoutDirection}
            container={true}
            ref={(ref) => connect(drag(ref))}
            className={selected ? classes.selected : ""}
            style={{
                // Background
                backgroundAttachment: `${backgroundAttachment}`,
                backgroundBlendMode: `${backgroundBlendMode}`,
                backgroundClip: `${backgroundClip}`,
                backgroundImage: `url(${backgroundImage})`,
                backgroundColor: `${backgroundColor}`,
                backgroundOrigin: `${backgroundOrigin}`,
                backgroundPosition: `${backgroundPosition}`,
                backgroundRepeat: `${backgroundRepeat}`,
                backgroundSize: `${backgroundSize}`,

                // Border
                borderColor: `${borderColor}`,
                borderStyle: `${borderStyle}`,
                borderWidth: `${borderWidth}${lengthCssUnits[borderWidthUnit]}`,

                // Border Image
                borderImageSource: `url(${borderImageSource})`,
                borderImageOutset: `${borderImageOutset}${borderImageOutsetUnit}`,
                borderImageRepeat: `${borderImageRepeat}`,
                borderImageSlice: `${borderImageSlice}%`,
                borderImageWidth: `${borderImageWidth}${borderImageWidthUnit}`,

                // Box
                boxSizing: `${boxSizing}`,
                boxShadow: `${boxShadow}`,

                // Dimension
                width: `${width}${lengthCssUnits[widthUnit]}`,
                height: `${height}${lengthCssUnits[heightUnit]}`,
                minWidth: `${minWidth}${lengthCssUnits[minWidthUnit]}`,
                minHeight: `${minHeight}${lengthCssUnits[minHeightUnit]}`,
                maxWidth: `${maxWidth}${
                    maxWidth !== "none" ? lengthCssUnits[maxWidthUnit] : ""
                }`,
                maxHeight: `${maxHeight}${
                    maxHeight !== "none" ? lengthCssUnits[maxHeightUnit] : ""
                }`,

                // Margin
                marginTop: `${marginTop}${lengthCssUnits[marginTopUnit]}`,
                marginRight: `${marginRight}${lengthCssUnits[marginRightUnit]}`,
                marginBottom: `${marginBottom}${lengthCssUnits[marginBottomUnit]}`,
                marginLeft: `${marginLeft}${lengthCssUnits[marginLeftUnit]}`,

                // Padding
                paddingTop: `${paddingTop}${lengthCssUnits[paddingTopUnit]}`,
                paddingRight: `${paddingRight}${lengthCssUnits[paddingRightUnit]}`,
                paddingBottom: `${paddingBottom}${lengthCssUnits[paddingBottomUnit]}`,
                paddingLeft: `${paddingLeft}${lengthCssUnits[paddingLeftUnit]}`,

                // Transform
                transform: makeTransform(
                    transformRotate,
                    transformRotateUnit,
                    transformTranslateX,
                    transformTranslateXUnit,
                    transformTranslateY,
                    transformTranslateYUnit,
                    transformScaleX,
                    transformScaleY,
                    transformSkewX,
                    transformSkewXUnit,
                    transformSkewY,
                    transformSkewYUnit
                ),

                // Outline
                ...outlineProperties,

                // Background Filter
                backdropFilter: makeBackdropFilter(
                    backgroundFilterBlur,
                    backgroundFilterBlurUnit,
                    backgroundFilterBrightness,
                    backgroundFilterContrast,
                    backgroundFilterGreyscale,
                    backgroundFilterHueRotate,
                    backgroundFilterHueRotateUnit,
                    backgroundFilterInvert,
                    backgroundFilterOpacity,
                    backgroundFilterSaturate,
                    backgroundFilterSepia
                ),
            }}
        >
            {children}
        </GridBase>
    );
}

GridComponent.craft = {
    defaultProps: {
        _accordionIndex: -1,

        // Layout

        layoutDirection: "row",

        // Dimensions
        width: 500,
        widthUnit: "px",
        height: 500,
        heightUnit: "px",
        minWidth: "0",
        minWidthUnit: "px",
        minHeight: "0",
        minHeightUnit: "px",
        maxWidth: "none",
        maxWidthUnit: "px",
        maxHeight: "none",
        maxHeightUnit: "px",

        // Margin
        marginTop: "0",
        marginTopUnit: "px",
        marginRight: "0",
        marginRightUnit: "px",
        marginBottom: "0",
        marginBottomUnit: "px",
        marginLeft: "0",
        marginLeftUnit: "px",

        // Padding
        paddingTop: "0",
        paddingTopUnit: "px",
        paddingRight: "0",
        paddingRightUnit: "px",
        paddingBottom: "0",
        paddingBottomUnit: "px",
        paddingLeft: "0",
        paddingLeftUnit: "px",

        // Transform
        transformRotate: 0,
        transformRotateUnit: "deg",
        transformTranslateX: 0,
        transformTranslateXUnit: "px",
        transformTranslateY: 0,
        transformTranslateYUnit: "px",
        transformScaleX: 0,
        transformScaleY: 0,
        transformSkewX: 0,
        transformSkewXUnit: "deg",
        transformSkewY: 0,
        transformSkewYUnit: "deg",

        // Outline
        outlineStyle: "none",
        outlineWidth: "1",
        outlineWidthUnit: "px",
        outlineColor: "",
        outlineOffset: "0",
        outlineOffsetUnit: "px",

        // Background Filter
        backgroundFilterBlur: "0",
        backgroundFilterBlurUnit: "px",
        backgroundFilterBrightness: "1.0",
        backgroundFilterContrast: "1.0",
        backgroundFilterGreyscale: "0.0",
        backgroundFilterHueRotate: "0",
        backgroundFilterHueRotateUnit: "deg",
        backgroundFilterInvert: "0",
        backgroundFilterOpacity: "1.0",
        backgroundFilterSaturate: "1.0",
        backgroundFilterSepia: "1.0",
        // TODO: drop shadow
    },
    related: {
        settings: GridSettings,
    },
};

export default GridComponent;

