function ButtonComponent({ size, variant, color, text, fullWidth, ripple }) {
    const {
        connectors: { connect, drag },
        selected,
        dragged,
    } = useNode((state) => ({
        selected: state.events.selected,
        dragged: state.events.dragged,
    }));

    return (
        <Button
            ref={(ref) => connect(drag(ref))}
            size={size}
            variant={variant}
            color={color}
            fullWidth={fullWidth}
            disableRipple={!ripple}
        >
            {text}
        </Button>
    );
}

