import React from "react";
import Fab from "@material-ui/core/Fab";
import { useNode } from "@craftjs/core";
import EditIcon from "@material-ui/icons/Edit";
import RecordForm, { extractValues } from "../../common/RecordForm";
import Accordion from "@material-ui/core/Accordion";
import AccordionDetails from "@material-ui/core/AccordionDetails";
import AccordionSummary from "@material-ui/core/AccordionSummary";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import Typography from "@material-ui/core/Typography";
import ViewSettings from "../ViewSettings";
function FabComponent(props) {
    const { size, variant, color, text } = props;
    const {
        connectors: { connect, drag },
        selected,
        dragged,
    } = useNode((state) => ({
        selected: state.events.selected,
        dragged: state.events.dragged,
    }));

    return (
        <Fab
            color={color}
            size={size}
            variant={variant}
            ref={(ref) => connect(drag(ref))}
        >
            <EditIcon />
            {text}
        </Fab>
    );
}

const groups = [
    {
        label: "Basic",
        children: [
            {
                label: "Color",
                identifier: "color",
                type: "select",
                options: [
                    {
                        value: "default",
                        title: "Default",
                    },
                    {
                        value: "primary",
                        title: "Primary",
                    },
                    {
                        value: "secondary",
                        title: "Secondary",
                    },
                ],
                required: false,
                readOnly: false,
                quickAdd: true,
                unique: false,
                hidden: false,
                tooltip: "The text alignment can be left, center, or right.",
                multipleValues: false,
                defaultValue: "default",
            },
            {
                label: "Size",
                identifier: "size",
                type: "select",
                options: [
                    {
                        value: "large",
                        title: "Large",
                    },
                    {
                        value: "medium",
                        title: "Medium",
                    },
                    {
                        value: "small",
                        title: "Small",
                    },
                ],
                required: false,
                readOnly: false,
                quickAdd: true,
                unique: false,
                hidden: false,
                tooltip: "The text alignment can be left, center, or right.",
                multipleValues: false,
                defaultValue: "medium",
            },
            {
                label: "Variant",
                identifier: "variant",
                type: "select",
                options: [
                    {
                        value: "extended",
                        title: "Extended",
                    },
                    {
                        value: "round",
                        title: "Round",
                    },
                ],
                required: false,
                readOnly: false,
                quickAdd: true,
                unique: false,
                hidden: false,
                tooltip: "The text alignment can be left, center, or right.",
                multipleValues: false,
                defaultValue: "medium",
            },
            {
                label: "Text",
                identifier: "text",
                type: "text",
                required: false,
                readOnly: false,
                quickAdd: false,
                unique: false,
                hidden: false,
                tooltip: "The first name of the user.",
                multipleValues: false,
                defaultValue: "",
            },
        ],
    },
];

function FabSettings() {
    const {
        actions: { setProp },
        size,
        variant,
        color,
        text,
        required,
    } = useNode((node) => ({
        size: node.data.props.size,
        variant: node.data.props.variant,
        color: node.data.props.color,
        text: node.data.props.text,
    }));

    const [saveDisabled, setSaveDisabled] = React.useState(true);
    const [values, setValues] = React.useState({
        ...extractValues(groups),
        size,
        variant,
        color,
        text,
    });
    const handleValueChange = (field, value) => {
        const newValues = JSON.parse(JSON.stringify(values));
        newValues[field.identifier] = value;

        setValues(newValues);

        setProp((props) => Object.assign(props, newValues));
    };

    return (
        <div>
            <ViewSettings />
            <Accordion>
                <AccordionSummary
                    expandIcon={<ExpandMoreIcon />}
                    id="panel1bh-header"
                >
                    <Typography>Checkbox Properties</Typography>
                </AccordionSummary>
                <React.Fragment>
                    <AccordionDetails>
                        <RecordForm
                            groups={groups}
                            values={values}
                            onValueChange={handleValueChange}
                            tabIndex={0}
                            showMore={true}
                            onValidityChange={setSaveDisabled}
                        />
                    </AccordionDetails>
                </React.Fragment>
            </Accordion>
        </div>
    );
}

FabComponent.craft = {
    defaultProps: {
        color: "primary",
        size: "small",
        variant: "round",
    },
    related: {
        settings: FabSettings,
    },
};

export default FabComponent;
