import React from "react";
import PropTypes from "prop-types";
import { makeStyles } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import PhoneIcon from "@material-ui/icons/Phone";
import FavoriteIcon from "@material-ui/icons/Favorite";
import PersonPinIcon from "@material-ui/icons/PersonPin";
import HelpIcon from "@material-ui/icons/Help";
import ShoppingBasket from "@material-ui/icons/ShoppingBasket";
import SettingsIcon from "@material-ui/icons/Settings";
import ActionsIcon from "@material-ui/icons/DeviceHub";
import NodesIcon from "@material-ui/icons/Layers";
import Typography from "@material-ui/core/Typography";
import Box from "@material-ui/core/Box";
import { Layers } from "@craftjs/layers";
import SettingsPanel from "./SettingsPanel";

function TabPanel(props) {
    const { children, value, index, ...other } = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`scrollable-prevent-tabpanel-${index}`}
            aria-labelledby={`scrollable-prevent-tab-${index}`}
            {...other}
        >
            {value === index && children}
        </div>
    );
}

TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.any.isRequired,
    value: PropTypes.any.isRequired,
};

function a11yProps(index) {
    return {
        id: `scrollable-prevent-tab-${index}`,
        "aria-controls": `scrollable-prevent-tabpanel-${index}`,
    };
}

const useStyles = makeStyles((theme) => ({
    root: {
        height: "100vh",
        flexGrow: 1,
        width: "100%",
        backgroundColor: theme.palette.background.paper,
    },
    tab: {
        minWidth: 80, // a number of your choice
        width: 80, // a number of your choice
    },
}));

export default function RightPanel() {
    const classes = useStyles();
    const [value, setValue] = React.useState(0);

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    return (
        <div className={classes.root}>
            <AppBar position="static">
                <Tabs
                    value={value}
                    onChange={handleChange}
                    variant="scrollable"
                    scrollButtons="auto"
                >
                    <Tab
                        classes={{ root: classes.tab }}
                        icon={<SettingsIcon />}
                    />
                    <Tab
                        classes={{ root: classes.tab }}
                        icon={<ActionsIcon />}
                    />
                    <Tab classes={{ root: classes.tab }} icon={<NodesIcon />} />
                </Tabs>
            </AppBar>
            <TabPanel value={value} index={0}>
                <SettingsPanel />
            </TabPanel>
            <TabPanel value={value} index={1}></TabPanel>
            <TabPanel
                value={value}
                index={2}
                style={{ color: "white !important" }}
            >
                <Layers />
            </TabPanel>
        </div>
    );
}
