export default function GridSettings() {
    const classes = useStyles();
    const {
        actions: { setProp },
        _accordionIndex,

        // Space
        spaceExtraLargeScreen,
        spaceLargeScreen,
        spaceMediumScreen,
        spaceSmallScreen,
        spaceExtraSmallScreen,

        // Background
        backgroundAttachment,
        backgroundBlendMode,
        backgroundClip,
        backgroundImage,
        backgroundColor,
        backgroundOrigin,
        backgroundPosition,
        backgroundRepeat,
        backgroundSize,

        // Border
        borderColor,
        borderStyle,
        borderWidth,
        borderWidthUnit,

        // Border Image
        borderImageSource,
        borderImageOutset,
        borderImageOutsetUnit,
        borderImageRepeat,
        borderImageSlice,
        borderImageWidth,
        borderImageWidthUnit,

        // Box
        boxSizing,
        boxShadow,

        // Dimension
        width,
        widthUnit,
        height,
        heightUnit,
        minWidth,
        minWidthUnit,
        minHeight,
        minHeightUnit,
        maxWidth,
        maxWidthUnit,
        maxHeight,
        maxHeightUnit,

        // Margin
        marginTop,
        marginTopUnit,
        marginRight,
        marginRightUnit,
        marginBottom,
        marginBottomUnit,
        marginLeft,
        marginLeftUnit,

        // Padding
        paddingTop,
        paddingTopUnit,
        paddingRight,
        paddingRightUnit,
        paddingBottom,
        paddingBottomUnit,
        paddingLeft,
        paddingLeftUnit,

        // Transform
        transformRotate,
        transformRotateUnit,
        transformTranslateX,
        transformTranslateXUnit,
        transformTranslateY,
        transformTranslateYUnit,
        transformScaleX,
        transformScaleY,
        transformSkewX,
        transformSkewXUnit,
        transformSkewY,
        transformSkewYUnit,

        // Outline
        outlineStyle,
        outlineWidth,
        outlineWidthUnit,
        outlineColor,
        outlineOffset,
        outlineOffsetUnit,

        // Background Filter
        backgroundFilterBlur,
        backgroundFilterBlurUnit,
        backgroundFilterBrightness,
        backgroundFilterContrast,
        backgroundFilterGreyscale,
        backgroundFilterHueRotate,
        backgroundFilterHueRotateUnit,
        backgroundFilterInvert,
        backgroundFilterOpacity,
        backgroundFilterSaturate,
        backgroundFilterSepia,
    } = useNode((node) => {
        console.log(node.data.props);
        return {
            ...node.data.props,
        };
    });

    const [accordionIndex, setAccordionIndex] = React.useState(_accordionIndex);
    const [saveDisabled, setSaveDisabled] = React.useState(true);
    const [values, setValues] = React.useState({
        // State
        spaceExtraLargeScreen,
        spaceLargeScreen,
        spaceMediumScreen,
        spaceSmallScreen,
        spaceExtraSmallScreen,

        // Background
        backgroundAttachment,
        backgroundBlendMode,
        backgroundClip,
        backgroundImage,
        backgroundColor,
        backgroundOrigin,
        backgroundPosition,
        backgroundRepeat,
        backgroundSize,

        // Border
        borderColor,
        borderStyle,
        borderWidth,
        borderWidthUnit,

        // Border Image
        borderImageSource,
        borderImageOutset,
        borderImageOutsetUnit,
        borderImageRepeat,
        borderImageSlice,
        borderImageWidth,
        borderImageWidthUnit,

        // Box
        boxSizing,
        boxShadow,

        // Dimension
        width,
        widthUnit,
        height,
        heightUnit,
        minWidth,
        minWidthUnit,
        minHeight,
        minHeightUnit,
        maxWidth,
        maxWidthUnit,
        maxHeight,
        maxHeightUnit,

        // Margin
        marginTop,
        marginTopUnit,
        marginRight,
        marginRightUnit,
        marginBottom,
        marginBottomUnit,
        marginLeft,
        marginLeftUnit,

        // Padding
        paddingTop,
        paddingTopUnit,
        paddingRight,
        paddingRightUnit,
        paddingBottom,
        paddingBottomUnit,
        paddingLeft,
        paddingLeftUnit,

        // Transform
        transformRotate,
        transformRotateUnit,
        transformTranslateX,
        transformTranslateXUnit,
        transformTranslateY,
        transformTranslateYUnit,
        transformScaleX,
        transformScaleY,
        transformSkewX,
        transformSkewXUnit,
        transformSkewY,
        transformSkewYUnit,

        // Outline
        outlineStyle,
        outlineWidth,
        outlineWidthUnit,
        outlineColor,
        outlineOffset,
        outlineOffsetUnit,

        // Background Filter
        backgroundFilterBlur,
        backgroundFilterBlurUnit,
        backgroundFilterBrightness,
        backgroundFilterContrast,
        backgroundFilterGreyscale,
        backgroundFilterHueRotate,
        backgroundFilterHueRotateUnit,
        backgroundFilterInvert,
        backgroundFilterOpacity,
        backgroundFilterSaturate,
        backgroundFilterSepia,
    });
    const handleValueChange = (field, value) => {
        const newValues = JSON.parse(JSON.stringify(values));
        newValues[field.identifier] = value;

        setValues(newValues);

        setProp((props) => Object.assign(props, newValues));
    };

    return (
        <div>
            {groups.map((group, index) => (
                <Accordion
                    key={group.label}
                    expanded={accordionIndex === index}
                    onChange={(event, expanded) => {
                        if (expanded) {
                            setAccordionIndex(index);
                            setProp((props) => (props._accordionIndex = index));
                        } else {
                            if (index == accordionIndex) {
                                setAccordionIndex(-1);
                                setProp(
                                    (props) => (props._accordionIndex = -1)
                                );
                            }
                        }
                    }}
                    elevation={0}
                >
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        id="panel1bh-header"
                    >
                        <Typography className={classes.heading}>
                            {group.label}
                        </Typography>
                    </AccordionSummary>
                    <AccordionDetails>
                        {index == accordionIndex && (
                            <RecordForm
                                groups={groups}
                                values={values}
                                onValueChange={handleValueChange}
                                tabIndex={accordionIndex}
                                showMore={true}
                                onValidityChange={setSaveDisabled}
                            />
                        )}
                    </AccordionDetails>
                </Accordion>
            ))}
        </div>
    );
}
