import React from "react";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import { useNode } from "@craftjs/core";
import RecordForm, { extractValues } from "../common/RecordForm";
import { makeStyles } from "@material-ui/core/styles";
import { withStyles } from "@material-ui/core/styles";
import MuiAccordion from "@material-ui/core/Accordion";
import MuiAccordionSummary from "@material-ui/core/AccordionSummary";
import MuiAccordionDetails from "@material-ui/core/AccordionDetails";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";

const Accordion = withStyles({
    root: {
        "&:not(:last-child)": {
            borderBottom: 0,
        },
        "&:before": {
            display: "none",
        },
        "&$expanded": {
            margin: "auto",
        },
    },
    expanded: {},
})(MuiAccordion);

const AccordionSummary = withStyles({
    root: {
        backgroundColor: "#FFFFFF",
        marginBottom: -1,
        minHeight: 20,
        "&$expanded": {
            minHeight: 20,
        },
    },
    content: {
        "&$expanded": {
            margin: "12px 0",
        },
    },
    expanded: {},
})(MuiAccordionSummary);

const AccordionDetails = withStyles((theme) => ({
    root: {
        padding: theme.spacing(2),
    },
}))(MuiAccordionDetails);

const useStyles = makeStyles((theme) => ({
    root: {
        margin: 0,
        width: "100%",
    },
    heading: {
        fontSize: theme.typography.pxToRem(15),
        flexBasis: "33.33%",
        flexShrink: 0,
    },
}));

const groups = [
    {
        label: "Basic",
        children: [
            {
                label: "Identifier",
                identifier: "identifier",
                type: "text",
                required: true,
                readOnly: false,
                quickAdd: false,
                unique: false,
                hidden: false,
                tooltip:
                    "The identifier of the view, should be unique within the current screen.",
                multipleValues: false,
                defaultValue: "view1",
            },
            {
                label: "Padding",
                identifier: "padding",
                type: "text",
                required: false,
                readOnly: false,
                quickAdd: false,
                unique: false,
                hidden: false,
                tooltip: "The padding of the view.",
                multipleValues: false,
                defaultValue: "",
            },
            {
                label: "Margin",
                identifier: "margin",
                type: "text",
                required: false,
                readOnly: false,
                quickAdd: false,
                unique: false,
                hidden: false,
                tooltip: "The margin of the view.",
                multipleValues: false,
                defaultValue: "",
            },
            {
                label: "Enabled",
                identifier: "enabled",
                type: "switch",
                required: false,
                readOnly: false,
                quickAdd: false,
                unique: false,
                hidden: false,
                tooltip: "The text of the button.",
                multipleValues: false,
                defaultValue: true,
            },
        ],
    },
];

function ViewSettings() {
    const classes = useStyles();

    const {
        actions: { setProp },
        size,
        variant,
        color,
    } = useNode((node) => ({
        size: node.data.props.size,
        variant: node.data.props.variant,
        color: node.data.props.color,
        text: node.data.props.text,
    }));

    const [saveDisabled, setSaveDisabled] = React.useState(true);
    const [values, setValues] = React.useState(extractValues(groups));
    const handleValueChange = (field, value) => {
        const newValues = JSON.parse(JSON.stringify(values));
        newValues[field.identifier] = value;

        setValues(newValues);

        setProp((props) => Object.assign(props, newValues));
    };

    return (
        <Accordion>
            <AccordionSummary
                expandIcon={<ExpandMoreIcon />}
                id="panel1bh-header"
            >
                <Typography className={classes.heading}>
                    View Properties
                </Typography>
            </AccordionSummary>
            <React.Fragment>
                <AccordionDetails>
                    <RecordForm
                        groups={groups}
                        values={values}
                        onValueChange={handleValueChange}
                        tabIndex={0}
                        showMore={true}
                        onValidityChange={setSaveDisabled}
                    />
                </AccordionDetails>
            </React.Fragment>
        </Accordion>
    );
}

export default ViewSettings;
