function RadioSettings() {
    const {
        actions: { setProp },
        text,
        value,
        placement,
        color,
        size,
    } = useNode((node) => ({
        size: node.data.props.size,
        color: node.data.props.color,
        text: node.data.props.text,
        value: node.data.props.value,
        placement: node.data.props.placement,
    }));

    const [saveDisabled, setSaveDisabled] = React.useState(true);
    const [values, setValues] = React.useState({
        ...extractValues(groups),
        text,
        value,
        placement,
        color,
        size,
    });
    const handleValueChange = (field, value) => {
        const newValues = JSON.parse(JSON.stringify(values));
        newValues[field.identifier] = value;

        setValues(newValues);

        setProp((props) => Object.assign(props, newValues));
    };

    return (
        <div>
            <ViewSettings />
            <Accordion>
                <AccordionSummary
                    expandIcon={<ExpandMoreIcon />}
                    id="panel1bh-header"
                >
                    <Typography>Button Properties</Typography>
                </AccordionSummary>
                <React.Fragment>
                    <AccordionDetails>
                        <RecordForm
                            groups={groups}
                            values={values}
                            onValueChange={handleValueChange}
                            tabIndex={0}
                            showMore={true}
                            onValidityChange={setSaveDisabled}
                        />
                    </AccordionDetails>
                </React.Fragment>
            </Accordion>
        </div>
    );
}

RadioComponent.craft = {
    defaultProps: {
        value: "",
        text: "Radio Text",
        placement: "end",
        size: "medium",
        color: "secondary",
    },
    related: {
        settings: RadioSettings,
    },
};

export default RadioComponent;
